@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
   margin: 0;
   font-family: "Quicksand";
   color: rgb(202, 202, 202);
   background-color: #1c1c1c;
}

.content {
   max-width: 800px;
   margin: 0 auto;
   padding: 20px;
}

.material-icons {
   -webkit-touch-callout: none !important;
   -webkit-user-select: none !important;
   -ms-user-select: none !important;
   user-select: none !important;
}

:root {
   --themeColor: rgb(202, 202, 202);
}

.blog-preview {
   background-color: #d6d6d611;
   border-radius: 5px;
   padding: 10px 16px;
   margin: 20px 0;
   transform: scale(1);
   transition: 0.2s;
}
.blog-preview:hover {
   background-color: #ffffff23;
   box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.281);
   transform: scale(1.01);
}

.blog-preview i {
   color: rgb(202, 202, 202);
   color: var(--themeColor);
}

.blog-preview p {
   color: rgb(202, 202, 202);
   color: var(--themeColor);
}

.blog-preview h2 {
   font-weight: 700;
   font-size: 20px;
   color: #f1356d;
   margin-bottom: 8px;
   margin-top: 2px;
}

.blog-list h3 {
   font-weight: 700;
}
.blog-list h4 a {
   color: #f1356d !important;
   border-radius: 2px;
   box-shadow: 0 0 0 0 rgb(253, 253, 253);
   transform: scale(1);
   animation: pulse 1s infinite;
}

@keyframes pulse {
   0% {
      transform: scale(0.95);
      box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.7);
   }

   70% {
      transform: scale(1);
      box-shadow: 0 0 0 10px rgba(255, 255, 255, 0);
   }

   100% {
      transform: scale(0.95);
      box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
   }
}

.blog-list h3 {
   position: relative;
   display: flex;
   align-items: center;
}
.new {
   position: absolute;
   right: 10px;
}
.new:hover {
   transform: scale(1.05);
}

.newfloater {
   width: 40px;
   height: 40px;
   border-radius: 50%;
   position: absolute;
   right: 10px;
   display: flex;
   align-items: center;
   justify-content: center;
}

:root {
   --themeColor2: #f1356d;
}

.nav-fixed {
   will-change: transform;
   z-index: 999;
   margin: 0 auto;
   background-color: transparent;
}

.nav-wrapper {
   background-color: #ffffff;
   border-bottom: 1px solid #f2f2f2;
}
.menu {
   color: #f1356d;
   color: var(--themeColor2);
}

.myTabs {
   font-weight: 900;
   font-size: 20px;
   color: #f1356d;
   color: var(--themeColor2);
}

@media only screen and (min-width: 976px) {
   .nav-fixed {
      margin-top: 20px;
      width: 900px;
   }
   .nav-wrapper {
      border-radius: 8px;
   }
   #nav-mobile li {
      width: 115px;
   }
}
@media only screen and (max-width: 976px) {
   .nav-fixed {
      top: 0;
      position: fixed;
   }
   .navSpace {
      width: 100%;
      height: 64px;
   }
   #nav-mobile li:nth-child(2) {
      height: 100%;
      width: 60px !important;
   }
}
@media only screen and (max-width: 600px) {
   .navSpace {
      height: 56px;
   }
}
@media only screen and (max-width: 600px) {
   #nav-mobile {
      width: 100px !important;
   }
   #nav-mobile li {
      margin-right: 5px;
   }
   .navIcons {
      width: 45px !important;
      height: 45px !important;
      border-radius: 50%;
   }
   .navIcons:hover {
      background-color: #f1356d !important;
      background-color: var(--themeColor2) !important;
      color: #fff !important;
   }
}
/*navIcons*/
#nav-mobile {
   width: 149px;
   height: 100%;
   display: flex;
   align-items: center;
   justify-content: center;
}
.navIcons {
   display: flex;
   align-items: center;
   justify-content: center;
   width: 100%;
   height: 100%;
}
#nav-mobile li a span,
#nav-mobile li a {
   margin: 0 !important;
   padding: 0 !important;
}
.brandLogo {
   position: absolute;
   color: #f1356d !important;
   color: var(--themeColor2) !important;
   font-weight: 700;
   margin-left: 30px;
   display: inline-block;
   font-size: 2.1rem;
}
#nav-mobile li {
   display: flex;
   align-items: center;
   justify-content: center;
}

.loader {
   border-radius: 4px;
   display: inline-block;
   width: 30px;
   height: 30px;
   position: relative;
   border: 4px solid #f1356d;
   top: 50%;
   animation: loader 2s infinite ease;
}

.loader-inner {
   vertical-align: top;
   display: inline-block;
   width: 100%;
   background-color: #f1356d;
   animation: loader-inner 2s infinite ease-in;
}

@keyframes loader {
   0% {
      transform: rotate(0deg);
   }

   25% {
      transform: rotate(180deg);
   }

   50% {
      transform: rotate(180deg);
   }

   75% {
      transform: rotate(360deg);
   }

   100% {
      transform: rotate(360deg);
   }
}

@keyframes loader-inner {
   0% {
      height: 0%;
   }

   25% {
      height: 0%;
   }

   50% {
      height: 100%;
   }

   75% {
      height: 100%;
   }

   100% {
      height: 0%;
   }
}

.contact h2 {
   color: #f1356d;
}
/* inputs */
.contact input {
   border-bottom: 2px solid #ddd !important;
   color: rgb(202, 202, 202);
   font-weight: 900;
   font-size: 20px !important;
   margin-bottom: 30px !important;
}
.contact input:focus {
   border-bottom: 1px solid #f1356d !important;
   box-shadow: 0 1px 0 0 #f1356d !important;
}
/* buttons */
.contact button {
   font-family: "Quicksand";
   font-weight: 900;
   font-size: 20px;
   width: 100px;
   background: #f1356d;
   color: #fff;
   border: 0;
   padding: 8px;
   border-radius: 8px;
   cursor: pointer;
}

/*modal*/
.modal {
   border-radius: 10px !important;
}
.modal p {
   color: #1c1c1c !important;
   font-size: 25px;
   font-weight: 900;
}
.modal-footer {
   background: #f2f2f2 !important;
}
.closeIcon {
   cursor: pointer;
   color: black;
}
.modal-overlay {
   pointer-events: none !important;
}

