.notfound_notFound__2JOlx a {
   font-size: 20px;
   font-weight: 700;
   color: #f1356d !important;
   border-radius: 2px;
   box-shadow: 0 0 0 0 rgb(253, 253, 253);
   transform: scale(1);
   animation: notfound_pulse__4FxCZ 1s infinite;
}

@keyframes notfound_pulse__4FxCZ {
   0% {
      transform: scale(0.95);
      box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.7);
   }

   70% {
      transform: scale(1);
      box-shadow: 0 0 0 10px rgba(255, 255, 255, 0);
   }

   100% {
      transform: scale(0.95);
      box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
   }
}

